<template>
  <div class="reg">
    <header class="wy-header">
      <div class="wy-header-icon-back" @click='goback'>
        <span></span>
      </div>
      <div class="wy-header-title">注册</div>
    </header>
    <div class="weui-content">
      <div class="weui-cells weui-cells_form wy-address-edit">
        <div class="weui-cell weui-cell_vcode">
          <div class="weui-cell__hd">
            <label class="weui-label wy-lab">用户名</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" placeholder="请输入用户名" v-model="username" />
          </div>
          <div class="weui-cell__ft">
            <button class="weui-vcode-btn">昵称</button>
          </div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label wy-lab">设置密码</label>
          </div>
          <div class="weui-cell__bd">
            <input class="weui-input" type="text" pattern="[0-9]*" placeholder="请输入新密码" v-model="password"/>
          </div>
        </div>
        <div class="file">
            <h1 id="hh" @click="selectIcon"></h1>
            <input type="file" id="icon" style="">
            <p>{{errMess}}</p>
        </div>
        <!-- <div>{{errMsg}}</div> -->
      </div>
      <label for="weuiAgree" class="weui-agree">
        <input id="weuiAgree" type="checkbox" class="weui-agree__checkbox" checked="checked" />
        <span class="weui-agree__text">
          阅读并同意
          <a href="javascript:void(0);">《注册协议》</a>
        </span>
      </label>
      <div class="weui-btn-area">
        <a href="javascript:;" class="weui-btn weui-btn_warn" @click="reg">注册</a>
      </div>
      <div class="weui-cells__tips t-c pd-10">
        <router-link class="weui-cell_link font-12" to='/know'>查看注册协议</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name:'reg',
    data(){
        return {
          username:'',
          password:'',
          errMess:''
        }
    },
    methods:{
        goback(){
            this.$router.go(-1);
        },
        selectIcon(){
            let input = document.getElementById('icon');
            input.click()
        },
        reg(){

          let formData = new FormData();
          formData.append('username',this.username)
          formData.append('password',this.password)
          let input = document.getElementById('icon')
          formData.append('icon',input.files[0])
          
          axios({
            url:'/api/reg',
            method:'post',
            data: formData
          }).then(
            res=>res.data.err===0?this.$router.push('/login'):this.errMess=res.data.msg
          )
        }
    }
}
</script>

<style scoped>
    .file{
      padding-top: 10px;
      width: 100%;
      height: 40px;
      text-align: center;
    }
    .file input{
      background:#fff;
      border:none;
    }

    @import url('../assets/css/style.css');
</style>