<template>
  <div class="login" style="background:#323542;">
    <div class="login-box">
      <div class="lg-title">欢迎登陆伟义商城</div>
      <div class="login-form">
        <form action="#">
          <div class="login-user-name common-div">
            <span class="eamil-icon common-icon">
              <img src="../assets/images/eamil.png" />
            </span>
            <input type="email" name="username" value placeholder="请输入您的账号" v-model="username" />
          </div>
          <div class="login-user-pasw common-div">
            <span class="pasw-icon common-icon">
              <img src="../assets/images/password.png" />
            </span>
            <input type="password" name="password" value placeholder="请输入您的密码" v-model="password"/>
            <p>{{errMsg}}</p>
          </div>
          <!-- <div>{{errMsg}}</div> -->
          <a href="javascript:;" class="login-btn common-div" @click="login">登陆</a>
          <a href="javascript:;" class="login-oth-btn common-div">微信登陆</a>
          <a href="javascript:;" class="login-oth-btn common-div">QQ登陆</a>
        </form>
      </div>
      <div class="forgets">
        <a href="psd_chage.html">忘记密码？</a>
        <router-link href="javascript:;" to="/reg">免费注册</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import * as  types from '../store/types';
// console.log(typeof(UPDATE_USER));
export default {
    name:'login',
    data(){
      return {
        username:'',
        password:'',
        errMsg:''
      }
    },
    methods:{

      login(){
        this.$store.dispatch(types.UPDATE_USER,{
            username:this.username,
            password:this.password
          }).then(
          res => {

            //发送dipsatch请求->actions->//抓取成功或者失败的数据，写入state, 同步localstorage->返回回执信息->login
            //login 确定后续的一个工作
            // console.log(res);
            // console.log(res.data.err  === 0);
            // if(!res.data.err){
            //   // console.log(this.$router);
            //   this.$router.push("/user");
            // }
            res.err === 0 ? this.$router.push('/user') : (this.errMsg = res.msg)
            });
      }

  }
}
</script>

<style scoped>
    @import url("../assets/css/style.css");
</style>