// 在此文件中，就是将组件从state那里获取到的内容，进行最后的操作，来将最终的结果返回，并进行渲染

export default {
   //将buycar中的数据与localstorage进行同步
    mapCar:(state) => {
        // 设置localstorage
        window.localStorage.setItem('buycar',JSON.stringify(state.buycar));
        // console.log(state.buycar);
        return 0;
    }
}