<script>
import store from '../plugins/vuex';
import {CLEAR_USER} from '../store/types';
export default {
    name:'user',
    data(){
      return {
        user:{}   
      }
    },
    // 用户前置守卫
    beforeRouteEnter(to,from,next){
      // 发送axios请求:跨域请求
      // 异步条件
      // axios({
      //   url:'/api/user'
      // }).then(
      //   res => {
      //     // console.log(res);
      //     res.data.err === 0 ? next(_this=>_this.user = res.data.data) : next('/login')}
      // )
  // 同步条件
    store.state.user.err === 0 ? next(_this=>_this.user =  store.state.user.data) : next('/login');
    },
    methods:{
      logout(){
        // 发送axios请求
        this.axios({
          url:"/api/logout",
          method:'delete'
        }).then(
        res => {
          if(res.data.err === 0) {
            // 删除localStorage，清空state.user
            this.$store.commit('CLEAR_USER');
            this.$store.commit('CLEAR_BUYCAR');
            // 删除buycar
            // this.$store.buycar=[];
            this.$router.push('/login'); 
            }
          });
      }
    }
}
</script>

