import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false;
import './assets/css/weui.css';
import './assets/css/jquery-weui.css';
import './assets/css/index.css';
import router from './plugins/router';
import store from './plugins/vuex';
// console.log(store);
import './plugins/axios';

// import { Loading } from 'vant';
// Vue.use(Loading);


import server from './config/server';
// 给实例的原型身上添加方法
Vue.prototype.baseUrl = server.baseUrl;

//同步状态管理和localStorage
let shop = window.localStorage.getItem('shop');
if(shop){
  store.commit('UPDATE_USER',JSON.parse(shop));
}


let buycars = window.localStorage.getItem('buycar');
if(buycars){
  store.commit('ADD_ITEM',JSON.parse(buycars));
}




let vm = new Vue({
  render: h => h(App),
  router,
  store,
}).$mount('#app')

export default vm;
