// 在这个文件中就是对state里面的数据进行状态的修改
import * as types from './types.js';

// 导出对象
export default {

    [types.VIEW_NAV]: (state,payload) => state.bNav = payload,
    [types.VIEW_LOADING]: (state,payload) => state.bLoading = payload,
    [types.VIEW_FOOT]: (state,payload) => state.bFoot = payload,
    [types.VIEW_CHECKBOX]: (state,payload) => state.checkBox = payload,

    [types.UPDATE_CHOOSE]:(state,payload) => state.choose = payload,
    [types.UPDATE_BANNER]: (state,payload) => state.banner = payload,
    [types.UPDATE_CLASSIFY]: (state,payload) => state.classify = payload,
    [types.UPDATE_WINE]: (state,payload) => state.wine = payload,
    [types.UPDATE_DETAIL]: (state,payload) => state.detail = payload,
    [types.UPDATE_USER]: (state, payload) => state.user = payload,

    
    [types.CLEAR_USER]: (state,payload) => {
        // 删除localStorage
        window.localStorage.removeItem('shop');
        // 修改state
        state.user = { err : 1 };
    },
    [types.CLEAR_BUYCAR]: (state,payload) => {
        // 删除localStorage
        window.localStorage.removeItem('buycar');
        // 修改state
        state.buycar = [];
    },
    [types.ADD_ITEM]:(state,payload)=> state.buycar=payload,

    [types.CHANGE_ITEM]:(state,payload)=> state.buycar=payload, //替换的是actions传递过来的新值 ,
    [types.REMOVE_ITEM]:(state,payload)=> state.buycar=payload,

    [types.DELETE_CART]:(state,payload)=> state.buycar=payload,
}