<template>
  <div id="app">
    <!-- 预加载页面 -->
    <Loading v-if="$store.state.bLoading"></Loading>
    <Heade v-if="bNav"></Heade>
    <router-view></router-view>
    <Foote v-if="bFoot"></Foote>
  </div>
</template>

<script>
import {mapState} from 'vuex';
import Heade from "./components/Heade";
import Foote from "./components/Foote";
import Loading from './components/Loading';
export default {
  name: "app",
  components: {
    Foote, Heade,Loading
  },
  computed:{
    ...mapState(['bNav','bFoot'])
  },
  // 属性检测，检测属性值来判断是否显示头部和尾部
  watch: {
    // 属性观测$route
    $route: {
      handler(nextRoute, prevRoute) {
        //  拿到跳转之后的对象
        // console.log(nextRoute.path);
        let path = nextRoute.path;
        // 正则匹配
        if (/classify|home/.test(path)) {
          this.$store.state.bNav = true;
          this.$store.state.bFoot = true;
        }
        if (/user/.test(path)) {
          this.$store.state.bNav = false;
          this.$store.state.bFoot = true;
        }
        if (/reg|login|detail|cart|business|know/.test(path)) {
          this.$store.state.bNav = false;
          this.$store.state.bFoot = false;
        }
        if (/choose|order|wine/.test(path)) {
          this.$root.bNav = true;
          this.$root.bFoot = false;
        }
      },
      immediate: true
    }
    
  }
};
</script>

