<template>
  <div class="cart" ontouchstart>
  <header class="wy-header">
  <div class="wy-header-icon-back" @click = 'goback'><span></span></div>
  <div class="wy-header-title">购物车</div>
</header>
<div class="weui-content">
  <div class="weui-panel weui-panel_access"
  v-for="item of buycars" :key="item._id">
    <div class="weui-panel__hd"><span>{{item.cat_one_id}}旗舰店</span><a href="javascript:;" class="wy-dele"></a></div>
    <div class="weui-panel__bd">
      <div class="weui-media-box_appmsg pd-10">
        <!-- <div class="weui-media-box__hd check-w weui-cells_checkbox"> -->
          <!-- <label class="weui-check__label" for="cart-pto1"> <div class="weui-cell__hd cat-check"> -->
            <input class="weui-media-box__hd check-w weui-cells_checkbox" type="checkbox"  name="cartpro"
             style="width: 20px; height: 20px;border:none;padding-right:10px;" @click="selectGoods(item)" v-model="item.checked">
            <!-- <i class="weui-icon-checked"	:class="{'ico_gou_on':item.checked}" 
            	></i> -->
              <!-- </div> -->
          <!-- </label> -->
        <!-- </div> -->
        <div class="weui-media-box__hd"><a href="#"><img class="weui-media-box__thumb" :src="item.goods_small_logo" alt=""></a></div>
        <div class="box">
          <h1 class="weui-media-box__desc"><a href="pro_info.html" class="ord-pro-link">{{item.goods_name}}</a></h1>
          <p class="weui-media-box__desc">规格：<span>{{item.goods_number}}</span></p>
          <div class="clear mg-t-10">
            <div class="wy-pro-pri fl">¥<em class="num font-15">{{item.goods_price}}</em></div>
            <div class="pro-amount fr"><div class="Spinner"></div></div>
          </div>
        </div>
        <div class="btn-group" role="group">
          <button type="button" class="btn" @click="CHANGE_ITEM({_id:item._id,num:-1})">-</button>
          <button type="button" class="btn" disabled>{{item.cart_number}}</button>
          <button type="button" class="btn" @click="CHANGE_ITEM({_id:item._id,num:1})">+</button>
        </div>
        <div class="delete"><input type="button" value="删除" @click="DELETE_CART({_id:item._id})"></div>
      </div>
    </div>
  </div>
</div>
<div class="goIndex"><button v-show="buycar.length <= 0" @click="goHome">回到首页</button></div>
<div class="clearCart"><button @click="CLEAR_BUYCAR" v-show="buycar.length > 0">清空购物车</button></div>
<!--底部导航-->
<div class="foot-black"></div>
<div class="weui-tabbar wy-foot-menud" style="padding:0 10px 0;">
  <input class="weui-media-box__hd check-w weui-cells_checkbox weui-icon-checke" type="checkbox"  name="cartpro"
      style="width: 20px; height: 20px;border:none;" @click="checkAll(!checkAllFlag)" v-model="checkedAll">
      <span style="padding:10px;">全选</span>
  <!-- <div class="npd cart-foot-check-item weui-cells_checkbox allselect">
    <label class="weui-cell allsec-well weui-check__label" for="all">
        <div class="weui-cell__hd">
          <input type="checkbox" class="weui-check" name="all-sec" id="all">
          <i class="weui-icon-checked" @click="checkAll(!checkAllFlag)"></i>
        </div>
        <div class="weui-cell__bd">
          <p class="font-14">全选</p>
        </div>
    </label>
  </div> -->
  <div class="weui-tabbar__item  npd">
    <p class="cart-total-txt">合计：<i>￥</i><em class="num font-16" id="zong1">{{totalPrice}}</em></p>
  </div>
  <a href="javascript:;" class="red-color npd w-90 t-c">
    <p class="promotion-foot-menu-label" @click="goIn">去结算</p>
  </a>
</div>
<div v-show='false'>{{mapCar}}</div>
</div>
</template>

<script>
import store from '../plugins/vuex';
import {mapState,mapActions,mapGetters,mapMutations} from 'vuex';
import * as types from '../store/types';
export default {
    name:'cart',
    data(){
      return {
        buycars:'',
        checkAllFlag:false,  //是否全选
        // totalMoney: 0,  //总价
        checkNum: 0,  //选择的商品数量(结算需要显示的数量)
        checkedAll:''//判断全选按钮
      }
    },
    mounted(){
      // this.$store.commit('VIEW_CHECKBOX',true);
      // this.totalPrice
      for(let i = 0; i < this.$store.state.buycar.length;i++){
          this.$store.state.buycar[i].checked = false;
      }
      // console.log(this);
    },
    methods:{
      ...mapActions([types.CHANGE_ITEM,types.REMOVE_ITEM,types.CLEAR_BUYCAR,types.DELETE_CART]),
      ...mapMutations([types.VIEW_CHECKBOX]),
      goback(){
        this.$router.go(-1);
      },
      goIn(){
        for(let i = 0; i < this.buycars.length;i++){
          if(this.buycars[i].checked === true){
            this.$store.state.money.push(this.buycars[i]);
          }
        }
        this.$router.push('/money');
      },
      goHome(){
        this.$router.push('/home');
      },
      // (单选)选择商品
      selectGoods(item){
        // console.log(item);
        // console.log(this.buycars);
        //判断是否未定义，如果没点击过按钮是没有注册的，则需要先注册checked属性
        if(typeof item.checked == 'undefined'){
          this.$set(item,"checked",true);
          this.checkNum ++;  //结算需要显示的数量
        }else{
          // console.log(2323);
          //  如果已经注册，则设置checked否(这里不能设置为false,因为当已经注册过之后再点击为flase，那么再点击一次则为true)
          item.checked = !item.checked;
          item.checked ? this.checkNum ++ : this.checkNum --;
        }
        // 当所有的商品都选择的时候，自动默认为全选
        if(this.buycars.length == this.checkNum){
          this.checkAll(true);
          this.checkedAll = true;
        }else{
          this.checkAllFlag = false;
          this.checkedAll = false;
        }
      },
      // 全选，点击全选时flag为true,取消时为false
      checkAll(flag){
        // console.log(this.buycars);
        this.checkAllFlag = flag;
        var _this = this;
        flag ? this.checkNum = this.buycars.length : this.checkNum = 0;
        this.buycars.forEach(function(item,index){
          if(typeof item.checked == 'undefined'){//也要防止未定义
            _this.$set(item,"checked",_this.checkAllFlag);//通过set来给item添加属性checked
          }else{
            item.checked = _this.checkAllFlag;
          }
        });
      },
      
    },
    computed:{
      ...mapState(['buycar']),
      ...mapGetters(['mapCar']),
      // 求总价
      totalPrice(){
        this.buycars = this.$store.state.buycar;
        // console.log(typeof this.$store.state.buycar);
        // console.log(this.buycars.length);
        var totalMoney = 0;
        for(var i =0;i<this.buycars.length;i++){
          // console.log(this.buycars);
          if(this.buycars[i].checked === true){
            totalMoney += this.buycars[i].goods_price * this.buycars[i].cart_number;
          }
          // console.log(this.buycars);
          console.log(totalMoney);
        }
        return totalMoney;
      },
      
      // allMoney(){
      //   let result = 0;
      //   if()
      // }
    },
    // 前置守卫
    beforeRouteEnter(to,from,next){
//       // 发送axios请求:跨域请求
//       axios({
//         url:'/api/user'
//       }).then(
//         res => {
//           // console.log(res);
          store.state.user.err === 0 ? next() : next('/login')}
//       )
  
}
</script>

<style scoped>
  /* .inputx{width: 25px;height} */
  .box{width: 140px;height: 100px;margin-right:10px;}
  .btn-group{width: 110px;}
  .btn-group button.btn{width: 30px;height: 30px;background: #cddddc;}
  .delete{font:14px "";line-height: 20px;}
  .delete input{background: #fff;}
  .clearCart{position: fixed;right: 20px;bottom: 70px;line-height: 40px;height: 40px;}
  .clearCart button{background: #cdcdcd;width:120px;}
  .goIndex{line-height: 40px;height: 40px;margin:auto;position: fixed;left:40%;top: 40%;}
  .goIndex button{background: red;width:120px;}
  /* .inputz{position:fixed;z-index: 222;} */
    @import url('../assets/css/style.css');
</style>